html,
body,
#root {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.App {
  width: 100%;
  display: flex;
  justify-content: center;
}

.container {
  background: white;
  width: 90%;
  max-width: 800px;
  min-height: 80vh;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Center content vertically if needed */
  position: relative;
  /* For absolute positioning if needed */
}

h2 {
  text-align: center;
  color: #1e3c72;
}

input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  background: #1e88e5;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background: #1565c0;
}